nav {
    background-color: goldenrod;
}

a {
    text-decoration: none;
    color: black;
}
 a:hover {
    text-decoration: underline;
    color: white;
}

.navbar-brand {
    font-size: 24px;
}

.nav-item {
    font-size: 20px;
}
#title {
    color: goldenrod;
}

.jumbotron {
    background-color: black;
}
.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

#test {
  background-color: goldenrod !important;
  color: black;
}

#test:hover {
  background-color: black !important;
  color: goldenrod;
}

img {
  padding-left: 5px;
  padding-top: 5px;
  padding-bottom: 5px;
}
#searchBtn {
    background-color: goldenrod;
    color: black;
}

#searchBtn:hover {
    background-color: black;
    color: goldenrod;
}

.form-group, h2 {
    text-align: center;
}
